@charset "utf-8"; 
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,600;1,100;1,200&family=Noto+Sans+KR:wght@100;400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Arapey&family=Cormorant:ital,wght@1,300&family=Old+Standard+TT:wght@400;700&family=Playfair+Display:wght@700&family=Readex+Pro:wght@700&display=swap');
:root * { font-family: 'Montserrat', sans-serif; font-family: 'Noto Sans KR', sans-serif; letter-spacing: 0px; box-sizing: border-box;

}::-webkit-scrollbar { display: none; }
* { margin: 0px; padding: 0px; box-sizing: border-box; /* color: #000; */
}
body { font-size: 1rem; font-family: 'Montserrat', sans-serif; color: #000; background-color: #31333B; min-width: 320px; }
.noto { font-family: 'Noto Sans KR' l, sans-serif; }
ol, ul { list-style: none; }
a { text-decoration: none; color: inherit; }
button { border: none; background: transparent; }
section {  min-height: 100vh; display: flex;  align-items: center;  }
.container { width: 1000px; margin: 0 auto; }
.title { font-family: "Playfair Display", serif; font-size: 110px; line-height: 120%;  }
.rowItem{ display: flex;  gap: 20px;}
.rowItem ul{ flex: 1; }
.colItem{ display: flex; flex-direction: column; gap: 20px;}


/* 이미지 */
.imgz-wrap {width: 100%;  overflow: hidden;}
.imgz-wrap img { width:100%; height: 100%; object-fit: cover;}

/* 첫번재 섹션 */
.mg16{margin-right: 16px; margin-left: 16px; text-align: center;}
.intro-wrap {}
.intro-wrap ul p{ font-size: 18px; font-weight: 400; line-height: 230%; padding-top: 50px;  word-break: keep-all; }
.intro-wrap ul .underline--magical { display: inline-block; background-image: linear-gradient(120deg, #6caf93 0%, #8fd3f4 100%); background-repeat: no-repeat; background-size: 100% 0.2em; background-position: 0 88%; transition: background-size 0.25s ease-in; }
.intro-wrap ul .underline--magical:hover { background-size: 100% 90%; }



/* 두번째 섹션 */
.about-wrap .skill-inner ul { display: grid; gap: 20px; grid-template-columns: repeat(3,1fr); margin-top: 20px; }
.about-wrap .skill-inner li { height: 150px; border: 1px solid white; border-radius: 8px; justify-content: center; align-items: center; display: flex; flex-direction: column; }

/* 세번째 섹션 */
.contents-wrap ul { display: grid; gap: 20px; grid-template-columns: repeat(3,1fr); margin-top: 20px; }
.item { height: 300px; font-family: 'Raleway', sans-serif; position: relative; display: inline-block; overflow: hidden;  color: #ffffff; font-size: 16px; text-align: center; overflow: hidden; }
.item * { -webkit-box-sizing: border-box; box-sizing: border-box; -webkit-transition: all 0.25s ease; transition: all 0.25s ease; }
.contents-wrap .item:before { position: absolute; top: 10px; bottom: 10px; left: 10px; right: 10px; top: 100%; content: ''; background-color: rgba(51, 51, 51, 0.9); -webkit-transition: all 0.25s ease; transition: all 0.25s ease; -webkit-transition-delay: 0.25s; transition-delay: 0.25s; }
.contents-wrap .item img { width: 100%; height: 100%; object-fit: cover; }
.contents-wrap .item span { position: absolute; top: 0; bottom: 0; left: 0; right: 0; z-index: 1; align-items: center; display: flex; flex-direction: column; justify-content: center; }
.contents-wrap .item h3, .item h5 { margin: 0; opacity: 0; letter-spacing: 1px; }
.contents-wrap .item h3 { -webkit-transform: translateY(-100%); transform: translateY(-100%); text-transform: uppercase; font-weight: 400; -webkit-transition-delay: 0.05s; transition-delay: 0.05s; margin-bottom: 5px; }
.contents-wrap .item h5 { font-weight: normal; background-color: #FFC86B; padding: 3px 10px; -webkit-transform: translateY(-100%); transform: translateY(-100%); -webkit-transition-delay: 0s; transition-delay: 0s; }
.contents-wrap .item:hover:before, .contents-wrap .item.hover:before { top: 10px; -webkit-transition-delay: 0s; transition-delay: 0s; }
.contents-wrap .item:hover h3, .contents-wrap .item.hover h3, .contents-wrap .item:hover h5, .contents-wrap .item.hover h5 { -webkit-transform: translateY(0); transform: translateY(0); opacity: 1; }
.contents-wrap .item:hover h3, .item.hover h3 { -webkit-transition-delay: 0.3s; transition-delay: 0.3s; }
.contents-wrap .item:hover h5, .contents-wrap .item.hover h5 { -webkit-transition-delay: 0.2s; transition-delay: 0.2s; }
/* 네번째 섹션 */
.Lince-wrap { width: 100%;
}
.table-design { display: table; padding: 10px 0; margin: 20% 0; width: 100%; margin: auto; }
.table-design:not(:last-child) { border-bottom: 1px solid #ddd; }
.table-design span { display: table-cell; }
.table-design h3 { display: table-cell; width: 100px; vertical-align: top; font-size: 2em; font-weight: bold; }
.table-design .item { height: auto; display: table-cell; vertical-align: middle; text-align: left; }
.table-design .item li span:nth-child(1) { width: 40px; font-size: 1em; font-weight: bold; color: #ffffff; }
.section-fluid-main { position: relative; display: block; overflow: hidden;  padding-right: 15px; padding-left: 15px; margin-right: auto; margin-left: auto;  padding: 100px 0; }
.section-row { display: -ms-flexbox; display: grid; -ms-flex-wrap: wrap; grid-template-columns: repeat(2,1fr); gap: 20px; }
.section-row:hover .section-col { opacity: 0.1; }
.section-col { position: relative; width: 100%;  overflow: hidden; -ms-flex: 0 0 50%; flex: 0 0 50%;  transition: opacity 250ms linear; }
.section-row .section-col:hover { opacity: 1; }
.section { position: relative; display: block; width: 100%; overflow: hidden; cursor: pointer; ; }
.section-in { height: 350px; overflow: hidden; position: relative; display: block; width: 100%; overflow: hidden; border-radius: 6px; cursor: pointer; }
.section-in img { display: block; width: 100%; height: 100%; transition: transform 250ms linear; object-fit: cover; }
.section-col:hover .section-in img { transform: scale(1.1) rotate(-3deg); }
.hover-text { position: fixed; top: 0; left: 0; width: 100%; height: 100%; overflow: hidden; z-index: 100; pointer-events: none; display: -webkit-inline-flex; display: -ms-inline-flexbox; display: inline-flex; -webkit-align-items: center; -moz-align-items: center; -ms-align-items: center; align-items: center; -webkit-justify-content: center; -moz-justify-content: center; -ms-justify-content: center; justify-content: center; -ms-flex-pack: center; text-align: center; -ms-flex-item-align: center; align-self: center; mix-blend-mode: difference; }
.hover-text h2 { font-family: 'Poppins', sans-serif; font-weight: 800; font-size: 8vw; line-height: 1; color: #fff; opacity: 0; transform: scale(0.8); transition: transform 250ms linear, opacity 250ms ease; }
.section-col:hover + .hover-text h2 { opacity: 1; transform: scale(1); }
.logo { position: fixed; top: 25px; left: 25px; display: block; z-index: 1000; transition: all 250ms linear; }
.logo img { height: 26px; width: auto; display: block; transition: filter 250ms 700ms linear; }



/* 다섯번째 섹션 */
.contact-wrap { color: aliceblue; height: auto;  margin: 0 auto; }
/* footer*/
.card { background: #fff; border: 1px solid #c2c2c2; border-radius: 3px ; height: 320px; overflow: hidden; }
.card-top { color: #000; }
.card-top img { height: 200px; object-fit: cover; overflow: hidden; width: 100%; }
.card-bottom h3, .card-bottom p { color: #000; margin: 10px 20px; }



@media (max-width:767px) {
  .container { width: 100%; margin: 0 auto; padding-left: 16px; padding-right: 16px;}
  .rowItem{ display: flex;  gap: 8px; flex-direction: column;}
  .rowItem ul img{display: none;}
  section{min-height: 80vh;}
  .intro-wrap{ padding-top: 100px; }
  .about-wrap .skill-inner li{height: 130px;}
  .about-wrap .skill-inner ul{gap: 8px; text-align: center;}
  .section{height: 200px;}
  .section-row{gap: 8px;}
  .intro-wrap ul p{font-size: 17px; line-height: 200%;}
}

