@charset "utf-8"; @import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,600;1,100;1,200&family=Noto+Sans+KR:wght@100;400&display=swap');:root * { font-family: 'Montserrat', sans-serif; font-family: 'Noto Sans KR', sans-serif; letter-spacing: 0px; box-sizing: border-box; }
header {  position: fixed;  width: 100%; margin: 0 auto; z-index: 10; padding: 20px 0; background-color: #31333B;  }
header .menu{display: flex ;}
header .header__nav{margin-left: auto;}
/* 서브메뉴css */
.hidden { visibility: hidden; opacity: 0; }
.intro { position: fixed; display: grid; place-items: center; height: 100vh; width: 100%; background-color: white; z-index: 5; }
.intro__title { color: white; max-width: 45%; text-align: center; mix-blend-mode: difference; z-index: 2; transform: translateY(40px); }
.intro__background { position: absolute; top: 0; background-color: black; width: 50%; height: 100%; transform: scaleX(0); }
.intro__background--left { left: 0; transform-origin: left center; }
.intro__background--right { left: 50%; transform-origin: right center; }
.header { display: flex; justify-content: space-between; padding: 64px 0; max-width: 70vw; margin: 0 auto; }
.header__logo { display: flex; align-items: center; }
.header__logo .dot { display: inline-block; height: 24px; width: 24px; background-color: #6caf93; border-radius: 50%; }
.header__logo .dot:last-child { height: 16px; width: 16px; margin-left: 4px; }
.header__nav { display: flex; font-family: "Ageo", sans-serif; letter-spacing: 0.05em; text-transform: lowercase; margin-left: auto; }
.header__nav > *:not(:last-child) { margin-right: 8px; }


.slider {  min-height: 100vh; display: flex; align-items: center; }
.slider ul { display: grid; gap: 10px; grid-template-columns: repeat(3,1fr); margin-top: 150px;  justify-content: space-between;}

@media (max-width:786px) {
  header{width: 100%; margin: 0 auto;}
  .slider ul { display: grid; gap: 10px; grid-template-columns: 1fr; margin-top: 150px;  justify-content: space-between;}
}


